<template>
  <div id="portfolio">
    <b-container>
      <b-row>
        <b-col>
          <b-button-group vertical>
            <b-button
              v-if="loggedIn"
              v-b-modal.portfolio-modal
              @click="resetData"
              variant="success"
            >New Portfolio</b-button>
            <b-spinner class="mt-2" variant="warning" v-if="newPortfolioLoading"></b-spinner>
            <b-modal
              size="xl"
              id="portfolio-modal"
              ok-variant="success"
              cancel-variant="danger"
              ok-title="Submit"
              cancel-title="Cancel"
              title="Add new portfolio!"
              @ok="handleOk"
            >
              <form ref="form" @submit.stop.prevent="handleSubmit">
                <b-form-group
                  :state="titleState"
                  label="Title: "
                  label-for="name-input"
                  invalid-feedback="You need to enter a title"
                >
                  <b-form-input id="name-input" v-model="title" :state="titleState" required></b-form-input>
                </b-form-group>
                <b-form-group label="Content: " label-for="content-input">
                  <medium-editor
                    :text="content"
                    :options="options"
                    v-on:edit="processEditOperation"
                    custom-tag="div"
                  />
                </b-form-group>
                <b-form-group
                  :state="coverImageState"
                  label="Cover Image: "
                  label-for="cover-image-input"
                  invalid-feedback="You need to add a cover image"
                >
                  <b-form-file
                    id="cover-image-input"
                    accept="image/*"
                    browse-text="Select"
                    placeholder="Select or drag an image"
                    drop-placeholder="You can drop"
                    v-model="coverImageUrl"
                    :state="coverImageState"
                    required
                  ></b-form-file>
                </b-form-group>
                <b-form-group
                  label="Keywords: "
                  label-for="keywords-input"
                  invalid-feedback="Enter keywords comma seperated"
                >
                  <b-form-tags
                    id="keywords-input"
                    tag-pills
                    tag-variant="primary"
                    remove-on-delete
                    separator=","
                    duplicate-tag-text="Same keyword exists"
                    placeholder="Add keywords via pressing enter or comma"
                    v-model="keywords"
                  ></b-form-tags>
                </b-form-group>
              </form>
            </b-modal>
          </b-button-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col
          v-if="portfoliosLoading"
          class="d-flex flex-column align-items-center justify-content-center"
        >
          <b-spinner variant="success"></b-spinner>
        </b-col>
        <b-col
          v-if="!portfoliosLoading"
          class="d-flex flex-column align-items-center justify-content-center"
        >
          <p v-if="portfolioEntries.length == 0">No portfolio entries found</p>
          <div v-if="portfolioEntries.length != 0">
            <b-card
              v-for="(portfolio, ind) in portfolioEntries"
              :id="'portfolio' + portfolio._id.toString()"
              :key="portfolio._id"
              :title="portfolio.title"
              :img-src="portfolio.coverImageUrl"
              img-top
              tag="article"
              class="mb-2"
              style="max-width: 40rem; width:100%;"
            >
              <span v-html="portfolio.content"></span>
              <b-button-group>
                <b-button class="mr-2" :to="'/portfolio/'+portfolio._id" variant="primary">Read more</b-button>
                <b-button
                  v-if="loggedIn"
                  class="mr-2"
                  :to="'/edit-portfolio/'+portfolio._id"
                  variant="success"
                >Edit</b-button>
                <b-button
                  v-b-modal="'deletemodal'+ind"
                  v-if="loggedIn"
                  class="mr-2"
                  variant="danger"
                >
                  <b-spinner variant="warning" v-if="deleteLoading" small></b-spinner>
                  <span v-if="!deleteLoading">Sil</span>
                </b-button>
                <b-modal
                  :id="'deletemodal'+ind"
                  title="Delete"
                  ok-title="Delete"
                  cancel-title="Cancel"
                  ok-variant="danger"
                  cancel-variant="success"
                  @ok="onDeleteOk(portfolio._id)"
                >
                  <p class="my-4">Are you sure to delete?</p>
                </b-modal>
              </b-button-group>
              <template v-slot:footer>
                <b-card-text>Keywords:</b-card-text>
                <span
                  style="font-style: italic;"
                  v-for="keyword in portfolio.keywords"
                  :key="keyword"
                >{{keyword}},</span>
              </template>
            </b-card>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import editor from "vue2-medium-editor";
import store from "../store/index";
export default {
  data: function() {
    return {
      keywords: [],
      options: {
        placeholder: false,
        buttonLabels: "fontawesome",
        toolbar: {
          buttons: [
            "h1",
            "h2",
            "h3",
            "anchor",
            "bold",
            "italic",
            "unorderedlist",
            "orderedlist",
            "table"
          ]
        },
        extensions: {
          table: new MediumEditorTable()
        }
      },
      content: "",
      title: "",
      coverImageUrl: null,
      deleteLoading: false,
      portfoliosLoading: false,
      newPortfolioLoading: false,
      coverImageBase64: ""
    };
  },
  components: {
    "medium-editor": editor
  },
  computed: {
    titleState() {
      return this.title.length > 0;
    },
    coverImageState() {
      return this.coverImageUrl != null;
    },
    loggedIn() {
      return store.state.loggedIn;
    },
    portfolioEntries() {
      return store.state.portfolioEntries;
    }
  },
  created() {
    this.portfoliosLoading = true;
    store.dispatch("fetchPortfolios", this);
  },
  methods: {
    toBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
      });
    },
    handleOk(e) {
      e.preventDefault();
      this.handleSubmit();
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      return valid;
    },
    async handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return;
      }
      this.newPortfolioLoading = true;
      try {
        this.coverImageBase64 = await this.toBase64(this.coverImageUrl);
      } catch (error) {
        this.$bvToast.toast("An error occured: " + JSON.stringify(error));
        this.newPortfolioLoading = false;
        return;
      }

      store.dispatch("addPortfolioEntry", this);
      // Hide the modal manually
      this.$nextTick(() => {
        this.$bvModal.hide("portfolio-modal");
      });
    },
    processEditOperation(operation) {
      this.content = operation.api.origElements.innerHTML;
    },
    onDeleteOk(_id) {
      this.onDelete(_id);
    },
    onDelete(_id) {
      this.deleteLoading = true;
      store.dispatch("deletePortfolio", { vm: this, _id: _id });
    },
    resetData() {
      this.content = "";
      this.coverImageUrl = null;
      this.keywords = [];
      this.title = "";
    }
  }
};
</script>

<style>
#portfolio {
  width: 90%;
}
img {
  width: 100%;
}
</style>
